import { Injectable } from '@angular/core';
import { HttpClient, HttpParams } from '@angular/common/http';
import { Observable } from 'rxjs';
import { environment } from '../../environments/environment';
import {
  Travel,
  TravelResponse,
  TravelDetailResponse,
  TravelCreateRequest,
  TravelUpdateRequest,
  TravelResult,
  TravelResultResponse,
  TravelResultCreateRequest,
  TravelResultUpdateRequest,
} from '../models/travel.model';

@Injectable({
  providedIn: 'root',
})
export class TravelService {
  private baseUrl = environment.apiBaseUrl;

  constructor(private http: HttpClient) {}

  getTravels(
    search?: string,
    from?: string,
    to?: string,
    pageNumber: number = 1,
    pageSize: number = 10
  ): Observable<TravelResponse> {
    let params = new HttpParams()
      .set('PageNumber', pageNumber.toString())
      .set('PageSize', pageSize.toString());

    if (search) {
      params = params.set('Search', search);
    }
    if (from) {
      params = params.set('From', from);
    }
    if (to) {
      params = params.set('To', to);
    }

    return this.http.get<TravelResponse>(`${this.baseUrl}/Travel`, { params });
  }

  getTravelById(id: number): Observable<TravelDetailResponse> {
    return this.http.get<TravelDetailResponse>(`${this.baseUrl}/Travel/${id}`);
  }

  createTravel(travel: TravelCreateRequest): Observable<TravelDetailResponse> {
    return this.http.post<TravelDetailResponse>(
      `${this.baseUrl}/Travel`,
      travel
    );
  }

  updateTravel(
    id: number,
    travel: TravelUpdateRequest
  ): Observable<TravelDetailResponse> {
    return this.http.put<TravelDetailResponse>(
      `${this.baseUrl}/Travel/${id}`,
      travel
    );
  }

  deleteTravel(id: number): Observable<{
    success: boolean;
    message: string;
    data: boolean;
    errors: string[];
  }> {
    return this.http.delete<{
      success: boolean;
      message: string;
      data: boolean;
      errors: string[];
    }>(`${this.baseUrl}/Travel/${id}`);
  }

  // Travel Results (Achievements) API methods
  getTravelResults(travelId: number): Observable<TravelResultResponse> {
    return this.http.get<TravelResultResponse>(`${this.baseUrl}/TravelResult?travelId=${travelId}`);
  }

  createTravelResult(result: TravelResultCreateRequest): Observable<TravelResultResponse> {
    return this.http.post<TravelResultResponse>(`${this.baseUrl}/TravelResult`, result);
  }

  updateTravelResult(id: number, result: TravelResultUpdateRequest): Observable<TravelResultResponse> {
    return this.http.put<TravelResultResponse>(`${this.baseUrl}/TravelResult/${id}`, result);
  }

  deleteTravelResult(id: number): Observable<{
    success: boolean;
    message: string;
    data: boolean;
    errors: string[];
  }> {
    return this.http.delete<{
      success: boolean;
      message: string;
      data: boolean;
      errors: string[];
    }>(`${this.baseUrl}/TravelResult/${id}`);
  }
}
