import { Component, OnInit, signal } from '@angular/core';
import { TravelService } from '../../../services/travel.service';
import { TranslationService } from '../../../services/translation.service';
import { LangService } from '../../../services/lang.service';
import { Travel } from '../../../models/travel.model';
import {
  ConfirmationModalComponent,
  ConfirmationModalData,
} from '../../shared/confirmation-modal/confirmation-modal.component';

@Component({
  selector: 'app-travel-list',
  templateUrl: './travel-list.component.html',
  styleUrls: ['./travel-list.component.css'],
  standalone: false,
})
export class TravelListComponent implements OnInit {
  travels = signal<Travel[]>([]);
  loading = signal(false);
  viewMode = signal<'cards' | 'table'>('cards');
  currentLang = signal<'ar' | 'en'>('en');
  isRTL = signal(false);

  // Math property for template
  Math = Math;

  // Pagination state
  currentPage = signal(1);
  pageSize = signal(10);
  totalCount = signal(0);
  totalPages = signal(0);

  // Search and filter state
  searchTerm = signal('');
  fromDate = signal('');
  toDate = signal('');

  // Modal state
  showDeleteModal = signal(false);
  travelToDelete = signal<Travel | null>(null);
  deleteModalData = signal<ConfirmationModalData>({
    title: '',
    message: '',
    confirmText: '',
    cancelText: '',
    type: 'delete',
  });

  // Edit/Add modal state
  showEditModal = signal(false);
  showAddModal = signal(false);
  travelToEdit = signal<Travel | null>(null);

  // Success modal state
  showSuccessModal = signal(false);
  successMessage = signal('');

  // Details modal state
  showDetailsModal = signal(false);
  travelToView = signal<Travel | null>(null);

  constructor(
    private travelService: TravelService,
    private translationService: TranslationService,
    private langService: LangService
  ) {}

  ngOnInit(): void {
    this.loadTravels();
    this.langService.currentLang$.subscribe((lang) => {
      this.currentLang.set(lang);
      this.isRTL.set(lang === 'ar');
    });
  }

  loadTravels(): void {
    this.loading.set(true);

    // Convert date strings to ISO format for API
    let fromDateISO = undefined;
    let toDateISO = undefined;

    if (this.fromDate()) {
      fromDateISO = new Date(this.fromDate() + 'T00:00:00').toISOString();
    }

    if (this.toDate()) {
      toDateISO = new Date(this.toDate() + 'T23:59:59').toISOString();
    }

    this.travelService
      .getTravels(
        this.searchTerm() || undefined,
        fromDateISO,
        toDateISO,
        this.currentPage(),
        this.pageSize()
      )
      .subscribe({
        next: (response) => {
          if (response.success) {
            this.travels.set(response.data.items);
            this.totalCount.set(response.data.totalCount);
            this.totalPages.set(
              Math.ceil(response.data.totalCount / this.pageSize())
            );
          }
          this.loading.set(false);
        },
        error: (error) => {
          console.error('Error loading travels:', error);
          this.loading.set(false);
        },
      });
  }

  openDeleteModal(travel: Travel): void {
    this.travelToDelete.set(travel);
    this.deleteModalData.set({
      title: this.translate('travel.delete_travel'),
      message: this.translate('travel.confirm_delete_travel').replace(
        '%title%',
        travel.title
      ),
      confirmText: this.translate('travel.delete'),
      cancelText: this.translate('travel.cancel'),
      type: 'delete',
    });
    this.showDeleteModal.set(true);
  }

  onDeleteConfirm(): void {
    const travel = this.travelToDelete();
    if (travel) {
      this.travelService.deleteTravel(travel.id).subscribe({
        next: (response) => {
          if (response.success) {
            this.travels.update((travels) =>
              travels.filter((t) => t.id !== travel.id)
            );
            this.showDeleteModal.set(false);
            this.travelToDelete.set(null);
            this.successMessage.set(this.translate('travel.deleted_success'));
            this.showSuccessModal.set(true);
            setTimeout(() => {
              this.showSuccessModal.set(false);
            }, 3000);
          }
        },
        error: (error) => {
          console.error('Error deleting travel:', error);
          this.showDeleteModal.set(false);
          this.travelToDelete.set(null);
        },
      });
    }
  }

  onDeleteCancel(): void {
    this.showDeleteModal.set(false);
    this.travelToDelete.set(null);
  }

  openEditModal(travel: Travel): void {
    this.travelToEdit.set(travel);
    this.showEditModal.set(true);
  }

  openAddModal(): void {
    this.showAddModal.set(true);
  }

  onEditModalClose(): void {
    this.showEditModal.set(false);
    this.travelToEdit.set(null);
  }

  onAddModalClose(): void {
    this.showAddModal.set(false);
  }

  onTravelUpdated(updatedTravel: Travel): void {
    this.travels.update((travels) =>
      travels.map((t) => (t.id === updatedTravel.id ? updatedTravel : t))
    );
    this.showEditModal.set(false);
    this.travelToEdit.set(null);
    this.successMessage.set(this.translate('travel.updated_success'));
    this.showSuccessModal.set(true);
    setTimeout(() => {
      this.showSuccessModal.set(false);
    }, 3000);
  }

  onTravelAdded(newTravel: Travel): void {
    this.travels.update((travels) => [newTravel, ...travels]);
    this.showAddModal.set(false);
    this.successMessage.set(this.translate('travel.added_success'));
    this.showSuccessModal.set(true);
    setTimeout(() => {
      this.showSuccessModal.set(false);
    }, 3000);
  }

  closeSuccessModal(): void {
    this.showSuccessModal.set(false);
  }

  openDetailsModal(travel: Travel): void {
    this.travelToView.set(travel);
    this.showDetailsModal.set(true);
  }

  onDetailsModalClose(): void {
    this.showDetailsModal.set(false);
    this.travelToView.set(null);
  }

  translate(key: string): string {
    return this.translationService.translate(key);
  }

  formatDate(dateString: string): string {
    return new Date(dateString).toLocaleDateString();
  }

  getTransportModeIcon(mode: string): string {
    switch (mode.toLowerCase()) {
      case 'air':
        return this.translate('travel.transport_modes.air');
      case 'train':
        return this.translate('travel.transport_modes.train');
      case 'bus':
        return this.translate('travel.transport_modes.bus');
      case 'car':
        return this.translate('travel.transport_modes.car');
      case 'ship':
        return this.translate('travel.transport_modes.ship');
      default:
        return this.translate('travel.transport_modes.other');
    }
  }

  // Pagination methods
  onPageChange(page: number): void {
    this.currentPage.set(page);
    this.loadTravels();
  }

  onPageSizeChange(event: Event): void {
    const size = +(event.target as HTMLSelectElement).value;
    this.pageSize.set(size);
    this.currentPage.set(1);
    this.loadTravels();
  }

  onSearch(event: Event): void {
    const search = (event.target as HTMLInputElement).value;
    this.searchTerm.set(search);
    this.currentPage.set(1);
    this.loadTravels();
  }

  /**
   * Handle date filter changes
   * This method is called when user changes either fromDate or toDate
   * It resets to page 1 and reloads travels with new date filters
   */
  onDateFilter(): void {
    this.currentPage.set(1);
    this.loadTravels();
  }

  clearFilters(): void {
    this.searchTerm.set('');
    this.fromDate.set('');
    this.toDate.set('');
    this.currentPage.set(1);
    this.loadTravels();
  }

  getPageNumbers(): number[] {
    const pages: number[] = [];
    const totalPages = this.totalPages();
    const currentPage = this.currentPage();

    const start = Math.max(1, currentPage - 2);
    const end = Math.min(totalPages, currentPage + 2);

    for (let i = start; i <= end; i++) {
      pages.push(i);
    }

    return pages;
  }
}
