import { Component, EventEmitter, Input, Output, signal, OnInit, ViewChild } from '@angular/core';
import { TravelService } from '../../../services/travel.service';
import { TranslationService } from '../../../services/translation.service';
import { Travel, TravelResult } from '../../../models/travel.model';
import { ConfirmationModalComponent, ConfirmationModalData } from '../../shared/confirmation-modal/confirmation-modal.component';

@Component({
  selector: 'app-travel-details-modal',
  templateUrl: './travel-details-modal.component.html',
  styleUrls: ['./travel-details-modal.component.css'],
  standalone: false,
})
export class TravelDetailsModalComponent implements OnInit {
  @Input() travelId: number | null = null;
  @Input() isRTL = false;
  @Output() close = new EventEmitter<void>();

  travel = signal<Travel | null>(null);
  achievements = signal<TravelResult[]>([]);
  loading = signal(false);
  achievementsLoading = signal(false);
  errorMessage = signal('');
  showAddAchievement = signal(false);
  editingAchievement = signal<TravelResult | null>(null);
  achievementText = signal('');
  achievementToDelete = signal<TravelResult | null>(null);
  showDeleteConfirmation = signal(false);

  constructor(
    private travelService: TravelService,
    private translationService: TranslationService
  ) {}

  ngOnInit(): void {
    if (this.travelId) {
      this.loadTravelDetails();
      this.loadAchievements();
    }
  }

  loadTravelDetails(): void {
    if (!this.travelId) return;

    this.loading.set(true);
    this.errorMessage.set('');

    this.travelService.getTravelById(this.travelId).subscribe({
      next: (response) => {
        if (response.success) {
          this.travel.set(response.data);
        } else {
          this.errorMessage.set(
            response.message || this.translate('travel.load_error')
          );
        }
        this.loading.set(false);
      },
      error: (error) => {
        console.error('Error loading travel details:', error);
        this.errorMessage.set(this.translate('travel.load_error'));
        this.loading.set(false);
      },
    });
  }

  closeModal(): void {
    this.close.emit();
  }

  translate(key: string): string {
    return this.translationService.translate(key);
  }

  formatDate(dateString: string): string {
    if (!dateString) return '';
    const date = new Date(dateString);
    return date.toLocaleDateString('en-US', {
      year: 'numeric',
      month: 'long',
      day: 'numeric',
    });
  }

  formatDateTime(dateString: string): string {
    if (!dateString) return '';
    const date = new Date(dateString);
    return date.toLocaleString('en-US', {
      year: 'numeric',
      month: 'long',
      day: 'numeric',
      hour: '2-digit',
      minute: '2-digit',
    });
  }

  getTransportModeIcon(mode: string): string {
    // Return empty string as we'll use SVG icons in the template
    return '';
  }

  getTransportModeLabel(mode: string): string {
    return this.translate(`travel.transport_modes.${mode.toLowerCase()}`);
  }

  loadAchievements(): void {
    if (!this.travelId) return;

    this.achievementsLoading.set(true);
    this.travelService.getTravelResults(this.travelId).subscribe({
      next: (response) => {
        if (response.success) {
          this.achievements.set(response.data);
        }
        this.achievementsLoading.set(false);
      },
      error: (error) => {
        console.error('Error loading achievements:', error);
        this.achievementsLoading.set(false);
      },
    });
  }

  showAddAchievementForm(): void {
    this.showAddAchievement.set(true);
    this.editingAchievement.set(null);
    this.achievementText.set('');
  }

  editAchievement(achievement: TravelResult): void {
    this.editingAchievement.set(achievement);
    this.achievementText.set(achievement.achievements);
    this.showAddAchievement.set(false);
  }

  cancelAchievementForm(): void {
    this.showAddAchievement.set(false);
    this.editingAchievement.set(null);
    this.achievementText.set('');
  }

  saveAchievement(): void {
    if (!this.achievementText().trim() || !this.travelId) return;

    const achievementData = {
      travelId: this.travelId,
      achievements: this.achievementText().trim(),
    };

    if (this.editingAchievement()) {
      // Update existing achievement
      this.travelService.updateTravelResult(this.editingAchievement()!.id, {
        achievements: this.achievementText().trim(),
      }).subscribe({
        next: (response) => {
          if (response.success) {
            this.loadAchievements();
            this.cancelAchievementForm();
          }
        },
        error: (error) => {
          console.error('Error updating achievement:', error);
        },
      });
    } else {
      // Create new achievement
      this.travelService.createTravelResult(achievementData).subscribe({
        next: (response) => {
          if (response.success) {
            this.loadAchievements();
            this.cancelAchievementForm();
          }
        },
        error: (error) => {
          console.error('Error creating achievement:', error);
        },
      });
    }
  }

  deleteAchievement(achievement: TravelResult): void {
    this.achievementToDelete.set(achievement);
    this.showDeleteConfirmation.set(true);
  }

  confirmDeleteAchievement(): void {
    const achievement = this.achievementToDelete();
    if (!achievement) return;

    this.travelService.deleteTravelResult(achievement.id).subscribe({
      next: (response) => {
        if (response.success) {
          this.loadAchievements();
        }
        this.showDeleteConfirmation.set(false);
        this.achievementToDelete.set(null);
      },
      error: (error) => {
        console.error('Error deleting achievement:', error);
        this.showDeleteConfirmation.set(false);
        this.achievementToDelete.set(null);
      },
    });
  }

  cancelDeleteAchievement(): void {
    this.showDeleteConfirmation.set(false);
    this.achievementToDelete.set(null);
  }

  getDeleteConfirmationData(): ConfirmationModalData {
    return {
      title: this.translate('travel.delete_achievement'),
      message: this.translate('common.delete_confirmation_message'),
      confirmText: this.translate('common.delete'),
      cancelText: this.translate('common.cancel'),
      type: 'delete'
    };
  }
}
